import React from "react";
import Autocomplete from "react-google-autocomplete";
import GoogleMapReact from "google-map-react";
import { Dropdown } from "primereact/dropdown";
import location from "../../../assets/images/location.png";
import "primereact/resources/themes/nova-light/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";

const Marker = props => (
  <div className="marker" style={{ top: props.lat, left: props.lng }}>
    <img src={location} alt="location" />
  </div>
);

class LocationAndCategory extends React.Component {
  state = {
    city: null,
    lat: 40.1872023,
    lng: 44.55820900000003
  };

  onContinue = () => {
    this.props.moveNextPage();
  };

  placeSelectHandler = place => {
    this.setState({
      lat: place.geometry.location.lat(),
      lng: place.geometry.location.lng()
    });

    this.props.getLatLng(this.state.lat, this.state.lng);
  };

  render() {
    const { form, error } = this.state;
    const {
      onChange,
      categories,
      address,
      submitted,
      categoryId,
      onCategoryChange
    } = this.props;
    return (
      <div className="location-category">
        <h2 className="title">Location and category</h2>
        <p className="paragraph">
          Please select your project category and choose location of your
          proposed project
        </p>
        <div className="tab-label">
          <label>
            <span>Category</span>
            <Dropdown
              optionLabel="name"
              value={categoryId}
              options={categories}
              onChange={onCategoryChange}
              placeholder="Choose category"
              className={submitted && !categoryId ? "G-invalid-input" : ""}
            />
          </label>
        </div>
        <div className="tab-label">
          <label>
            <span>Location</span>
          </label>
          <Autocomplete
            style={{ width: "100%" }}
            onPlaceSelected={this.placeSelectHandler}
            types={["establishment", "geocode"]}
            placeholder="Choose location"
            className={submitted && !address ? "G-invalid-input" : ""}
          />
          <div
            style={{
              height: "450px",
              width: "100%",
              marginTop: "30px",
              filter: "grayScale(60%)"
            }}
          >
            <GoogleMapReact
              bootstrapURLKeys={{
                key: "AIzaSyBpKF69rgtgYwZWCPeqYhdmD-ARYWMBdQE"
              }}
              center={{
                lat: this.state.lat,
                lng: this.state.lng
              }}
              defaultZoom={16}
            />
            <Marker lat={this.state.lat} lng={this.state.lng} />
          </div>
        </div>
        <div className="map" />
        <button className="G-button" onClick={this.onContinue}>
          Continue
        </button>
      </div>
    );
  }
}

export default LocationAndCategory;
